// utils/getRecipientEmail.js

const getRecipientEmail = (users, userLoggedIn) => {
  // Return null if either users or userLoggedIn is missing
  if (!users || !Array.isArray(users) || users.length === 0 || !userLoggedIn?.email) {
    console.log("❌ getRecipientEmail: Missing or invalid data", { 
      users, 
      hasUser: !!userLoggedIn,
      usersLength: users?.length 
    });
    return null;
  }

  // Normalize emails to lowercase for comparison
  const normalizedUserEmail = userLoggedIn.email.toLowerCase();
  const normalizedUsers = users.map(email => email?.toLowerCase()).filter(Boolean);

  console.log("🔍 getRecipientEmail: Processing", { 
    originalUsers: users,
    normalizedUsers,
    normalizedUserEmail,
    usersLength: normalizedUsers.length,
  });

  // ✅ SIMPLIFIED: Check if all users are the same (self-chat indicator)
  const uniqueUsers = [...new Set(normalizedUsers)];
  const isSelfChat = uniqueUsers.length === 1 && uniqueUsers[0] === normalizedUserEmail;

  console.log("🔍 getRecipientEmail: Self-chat check", {
    uniqueUsers,
    currentUser: normalizedUserEmail,
    isSelfChat,
  });

  if (isSelfChat) {
    console.log("💬 getRecipientEmail: Self-chat CONFIRMED");
    return userLoggedIn.email;
  }

  // Filter out the current user's email to find the other participant
  const recipientEmails = users.filter(email => 
    email && email.toLowerCase() !== normalizedUserEmail
  );

  console.log("👥 getRecipientEmail: Found recipients", recipientEmails);

  // Return null if no recipient found
  if (recipientEmails.length === 0) {
    console.error("❌ getRecipientEmail: No recipient found - possible data issue");
    return null;
  }

  return recipientEmails[0];
};

export default getRecipientEmail;